import { Component, Host, h } from '@stencil/core';

@Component({
	tag: 'o-sidebar',
	styleUrl: 'o-sidebar.scss',
	shadow: true
})
export class OSidebar {
	render() {
		return (
			<Host>
				<div
					class={`shadow-sm-bottom bg-background-default w-full fixed left-0 top-0 z-50 rounded-r-radius-lg p-5 box-border`}
				>
					<div class="size-full rounded-r-radius-lg break-words overflow-auto">
						<slot></slot>
					</div>
				</div>
			</Host>
		);
	}
}
